$font-stack: "Open Sans", sans-serif;
$red-color: rgb(234, 125, 210);
$gray-color: rgb(128, 128, 128);

%flex-container-column {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 1rem;
}

@mixin social-list($size: 50px) {
	display: flex;
	gap: 1rem;

	.social-item {
		width: $size;
		height: $size;
		overflow: hidden;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

%center-element {
	max-width: 90%;
	margin: 0 auto;
}

%title {
	font-size: 2rem;
	text-align: center;
}

@mixin logo($size: 100px) {
	@extend %flex-container-column;
	gap: 0;

	img {
		width: $size;
		height: $size;
	}

	.logo__desc {
		align-self: flex-end;
		font-size: 0.5rem;
	}
}

.btn {
	color: black;
	text-decoration: none;
	border: 2px solid black;
	padding: 0.7rem 3rem;
	transition: background-color 150ms;
	&:hover {
		background-color: rgb(196, 196, 196);
	}
}

.link::after {
	content: "";
	display: block;
	width: 100%;
	height: 2px;
	background-color: black;
	position: relative;
	bottom: 1px;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

img {
	width: 30px;
	height: 30px;
}

body {
	font-family: $font-stack;
	min-height: 100vh;
}

.header-image-container {
	position: absolute;
	z-index: -1;
	width: 100vw;
	height: 100vh;

	& img {
		width: 20%;
		height: 100%;
		position: absolute;
		top: 0;
		display: none;
	}

	.header-image-left {
		left: 0;
	}
	.header-image-right {
		right: 0;
	}
}

.header {
	margin-top: 100px;
	@extend %flex-container-column;
	gap: 2rem;
	text-align: center;

	.logo {
		@include logo(130px);
	}

	& .nav {
		display: flex;
		gap: 1rem;
		align-items: center;
		& .nav__social-list {
			@include social-list();
		}
	}
}

.showcase {
	@extend %flex-container-column;
	width: 90%;
	max-width: 500px;
	box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.349);
	padding: 2rem;

	.showcase__title {
		@extend %title;
		color: $red-color;
		font-weight: 700;
	}

	.showcase__desc {
		color: $gray-color;
	}
}

.main {
	@extend %flex-container-column;

	& > .title {
		@extend %title;
		margin-top: 3rem;
	}
}

.product-container {
	@extend %flex-container-column;
	@extend %center-element;
	gap: 3rem;
	margin-top: 2rem;

	.product-item {
		@extend %flex-container-column;
		text-align: center;

		.product-name {
			@extend %title;
		}

		.product-image {
			height: 300px;
			width: 90%;
			img {
				width: 100%;
				height: 100%;
				object-fit: contain;
			}
		}

		.product-feature {
			img {
				display: inline;
				width: 20px;
				height: 20px;
			}
		}

		.product-promocode {
			.code {
				@extend %title;
				font-weight: 800;
			}
		}
	}
}

.feature-list {
	@extend %flex-container-column;
	@extend %center-element;
	max-width: 70%;

	align-items: flex-start;
	margin-top: 2rem;
	margin-bottom: 2rem;

	.feature-item {
		display: flex;
		align-items: center;
		gap: 1rem;
	}
}

.footer {
	margin-top: 3rem;
	box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.278);
	display: flex;
	justify-content: center;
	align-self: center;
	gap: 1rem;
	padding: 2rem;
	.left-side {
		@extend %flex-container-column;
		.logo {
			@include logo(60px);
		}
	}

	.footer__social-list {
		@include social-list(30px);
	}

	.contact-list {
		@extend %flex-container-column;

		.contact-item {
			display: flex;
			align-self: start;
			gap: 0.5rem;
			img {
				width: 20px;
				height: 20px;
			}
		}
	}
}
